import React from 'react';

import { Menu, Dropdown, Icon ,Layout,Avatar} from 'antd';
import './index.css'
import logo from '../../assets/logo.svg'
import logoText from '../../assets/text.svg'
const { Header } = Layout;
const menu = (
    <Menu className="menu" selectedKeys={[]} >
      <Menu.Item disabled><Icon type="user" />user</Menu.Item>
      <Menu.Item disabled><Icon type="setting" />setting</Menu.Item>
      <Menu.Divider />
      <Menu.Item key="logout"><Icon type="logout" />logout</Menu.Item>
    </Menu>
  );

class NHeader extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            collapsed: false
        };
        this.delta = this.delta.bind(this);
    }
    delta(){
        this.setState({
            collapsed: !this.state.collapsed,
        });
    }
    // getStyles = () => {
   
    //     return {
    //         well: {
    //             boxShadow: `5px 5px 5px red`,
    //             padding: 0,
    //             background: `linear-gradient(#FFFFFF, #EBEBEB)`,
    //             box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
    //         },
    //     };
    // }

    
    render(){
        // const styles = this.getStyles();
        return(
            <Header id ="header" className="clearfix" style={{ position: 'fixed', width: '100%' }}>
                    <div className="logo">
                        <img src={logo}/>
                        <img src={logoText}/>
                        <div className="right">
                        <Dropdown overlay={menu} style={{ height:"100px", backgroundcolor:"blue"}}>
                        <span className="action account">
                            
                                <Avatar size="medium" className="avatar"  /> 
                                <span style={{color:"black"}}> Admin</span>
                            
                            </span>
                        </Dropdown>
                    </div>
                    </div>

            </Header>
            
        )
    }
}


export default NHeader


